import React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

/*actions*/
import {loadCountries} from '../actions/loadCountries';
import {getDetails} from '../actions/getDetails';
import {closeInfo,openInfo} from '../actions/editInfo';

/*components*/
import Select from './Select';
import Map from './Map';
import DescriptionCountry from './DescriptionCountry';

class App extends React.Component {
  componentWillMount() {
    this.props.loadCountries();
  }
  changeDetails(details) {
    this.props.getDetails(details);
  }
  render() {
    return (
      <div>
        <Select options={this.props.countries} details={this.props.details} changeDetails={this.changeDetails.bind(this)}/>
        <DescriptionCountry details={this.props.details}/>
        <Map details={this.props.details} showInfo={this.props.showInfo} closeInfo={this.props.closeInfo.bind(this)} openInfo={this.props.openInfo.bind(this)}/>
      </div>
    );
  }
}

export default connect(
    (state) => {
      return {countries:state.countries,details:state.details,showInfo:state.showInfo};
    },
    (dispatch) => bindActionCreators({loadCountries,getDetails,closeInfo,openInfo},dispatch)
)(App);



